import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { OrderItem } from '../../components/order-item';
import { v4 as uuid } from 'uuid';
import { calcTotalPrice } from '../../components/utils';
import { useDispatch } from 'react-redux';
import { setEmptyCart } from '../../redux/cart/reducer';
import { BiCartAlt } from 'react-icons/bi';
import { Link, useNavigate } from 'react-router-dom';
import './order-page.css';
import { Popup } from '../../components/popup/popup';

const funDeclensionWords = require('numeralize-ru');

export const OrderPage = () => {
  const [cartIsEmpty, setCartIsEmpty] = useState(false);
  const items = useSelector((state) => state.cart.itemsInCart);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if (cartIsEmpty) {
      navigate('/game-store/');
    }
  }, [cartIsEmpty]);
  const handleClick = () => {
    alert('спасибо за покупку, возвращайтесь мы уже соскучились !');
    dispatch(setEmptyCart());
    setCartIsEmpty(true);
  };

  if (items.length < 1) {
    return <h1 className="text-align-center">Ваша корзина пуста</h1>;
  }
  return (
    <>
      <div className="order-page">
        <div className="order-page__left">
          {items.map((game) => (
            <OrderItem game={game} key={uuid()} />
          ))}
        </div>
        <div className="order-page__right">
          <div className="order-page__total-price">
            <div className="text-align-center">
              <span className="span">
                {items.length}{' '}
                {funDeclensionWords.pluralize(
                  items.length,
                  'игра',
                  'игры',
                  'игр',
                )}{' '}
                на сумму {calcTotalPrice(items)} rub.
              </span>
            </div>
            <Link
              onClick={handleClick}
              className="btn btn--primary align-btn-center"
            >
              buy <BiCartAlt size={18} className="cart-block__icon btn" />
            </Link>
          </div>
        </div>
      </div>
      <Link to={'/game-store/'} className="btn btn--primary align-btn-center">
        назад
      </Link>
    </>
  );
};
