import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setStatusPopup, setTogglePopup } from '../../redux/popup/popupSlice';
import './popup.css';

export const Popup = () => {
  const dispatch = useDispatch();
  const game = useSelector((state) => state.game.currentGame);

  return (
    <div
      onClick={() => {
        dispatch(setTogglePopup());
      }}
      className="popup-container"
    >
      <div className="popup">
        <img src={game.image} width="100%" alt="screenshot from game"></img>
      </div>
    </div>
  );
};
