import React from 'react';
import { useDispatch } from 'react-redux';
import { setStatusPopup, setTogglePopup } from '../../redux/popup/popupSlice';
import { BiZoomIn } from 'react-icons/bi';
import './game-cover.css';

export const GameCover = ({ image = '' }) => {
  const dispatch = useDispatch();
  const handleClick = (e) => {
    console.log('zoom   !handle click +');
    e.stopPropagation();
    dispatch(setStatusPopup(true));
    // console.log(e.target.previousElementSibling());
  };

  return (
    <>
      <BiZoomIn
        onClick={(e) => handleClick(e)}
        size={25}
        className="zoom cart-block__icon btn zoom"
      />
      <div
        onClick={(e) => {
          // e.stopImmediatePropagation();
          console.log('alert');
          // dispatch(setStatusPopup());
        }}
        className="game-cover"
        style={{ backgroundImage: `url(${image})` }}
      ></div>
    </>
  );
};
